// 课堂满意度
import React, { Component } from 'react';
import { Col } from 'antd';
import OtherChart from '../../../common/components/BaseEcharts/OtherChart';
import jsonData from '../../../data/course/课程视角--教学评价--课堂满意度.json';

export default class ClassSatisfied extends Component {

  render() {
    let option = {
      title: {
        show: false,
      },
      grid: {
        top: '50%',
        // left: '3%',
        // right: '3%',
        // bottom: '25%',
        containLabel: false,
      },
      tooltip: {},
      legend: {
        show: false,
      },
      radar: {
        center: ['50%', '55%'],
        axisLine: {
          lineStyle: {
            color: '#00CFFF',
          },
        },
        name: {
          textStyle: {
            color: '#00CFFF',
            borderRadius: 3,
            padding: [0, 0],
          },
        },
        nameGap :5,
        indicator: [
          { name: '课堂到课率', max: 100 },
          { name: '线上活跃率', max: 100 },
          { name: '知识掌握率', max: 100 },
          { name: '线下互动率', max: 100 },
          { name: '教师响应率', max: 100 },
        ],
      },
      series: [{
        name: '',
        type: 'radar',
        areaStyle: {
          normal: {
            color: '#00CFFF',
          },
        },
        lineStyle: {
          color: '#00CFFF',
        },
        symbolSize:0,
        data: [
          {
            value: jsonData.RECORDS,
            name: '课堂满意度',
          },
        ],
      }],
    };
    return (
      <Col span={24} style={{height:'100%'}}>
        <OtherChart
          id='ClassSatisfied'
          data={option}
          height='100%'/>
      </Col>
    );
  }
}
