/**
 *其他图共用chart
 */
import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import {Col} from 'antd';
import echarts from 'echarts/lib/echarts';
import 'echarts/map/js/china.js';
import TipInfoComp from './TipInfoComp'

class OtherChart extends Component {
    constructor(props) {
        super(props);
        this.state = {
            chart: ''
        }
    }
    componentDidMount() {
    }
    render() {
        let chartData = this.props.data;
        if(chartData && chartData !== 'nodata'){
            let option=chartData;
            let id = this.props.id;
            let Interval = setInterval(() => {
                if (document.getElementById(id)) {
                    clearInterval(Interval);
                    let myCharts = echarts.init(document.getElementById(id));
                    myCharts.off('click');
                    myCharts.resize();
                    // myCharts.clear();
                    myCharts.setOption(option);
                    /**
                     * 点击统计图
                     */
                    if (this.props.clickChart) {
                        myCharts.on('click', (params) => {
                            this.props.clickChart(params);
                        })
                    }
                }
            }, 20)
        }
        return (
            <div style={{ height: this.props.height ? this.props.height : '400px' }}>
                {
                    chartData && chartData !== 'nodata'?<Col 
                        span={24} 
                        className="map_echarts" 
                        id={this.props.id} 
                        style={{ height: this.props.height ? this.props.height : '400px' }}>
                        </Col>:<TipInfoComp data={this.state.chartData} imgtype={this.props.imgtype}></TipInfoComp>
                }
            </div>
        )
    }
}

export default withRouter(OtherChart)