import React, {Component} from 'react';
import {Col} from 'antd';
import LineArrayCharts from '../../../common/components/BaseEcharts/LineArrayCharts';
import echarts from 'echarts';
import jsonData from '../../../data/course/课程视角--课程教学--教学资源情况.json';
export default class InstructionalResource extends Component {
  constructor(props) {
    super(props);
    this.state = {

    }
  }
  render(){
        let pieOption = {
            title:{
              textStyle:{
                  color:"#6a9cd5",
              },
              left:"center",
            },
            tooltip: {
                trigger:'axis',
                formatter:"{b} : {c} "
            },
            grid: {//统计图距离边缘的距离
                top: '8%',
                left: '10%',
                right: '10%',
                bottom: '10%'
            },
            xAxis: [{
                type: 'category',
                boundaryGap: false,
                axisLine: {
                    show: true,
                    lineStyle: {
                        color: '#233e64'
                    },
                },
                axisLabel : {
                    textStyle: {
                        color: '#00CFFF',
                    },
                    formatter : function(params){
                        var newParamsName = "";
                        var paramsNameNumber = params.length;
                        var provideNumber = 5;
                        var rowNumber = Math.ceil(paramsNameNumber / provideNumber);
                    if (paramsNameNumber > provideNumber) {
                        for (var p = 0; p < rowNumber; p++) {
                            var tempStr = "";
                            var start = p * provideNumber;
                            var end = start + provideNumber;
                            if (p === rowNumber - 1) {
                                tempStr = params.substring(start, paramsNameNumber);
                            } else {
                                tempStr = params.substring(start, end) + "\n"; } newParamsName += tempStr;
                            }
                    } else {
                            newParamsName = params;
                        }
                        return newParamsName
                    }
                },
                // axisTick: { show: true,},//刻度点数轴
                data: jsonData.RECORDS.axis,
            }],
            yAxis:{
                axisLine: {
                show: false
                },
                axisTick: {
                    show: false
                },
                axisLabel: {
                  color:'#00CFFF'
                },
                splitLine:{
                    // show:false,
                    lineStyle: {
                      color: "#379fb7",
                      type: "dashed"
                    }
                }
            },
            series: [{
                name: '',
                type: 'line',
                smooth: true,
                symbolSize:0,
                lineStyle: {
                    normal: {
                        color: "#3deaff"
                    }
                },
                areaStyle: {
                    normal: {
                        color: new echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                              {offset: 0, color: 'rgba(0,207,255,1)'},
                              {offset: 0.5, color: 'rgba(0,207,255,0.8)'},
                              {offset: 1, color: 'rgba(0,207,255,0.19)'}
                            ]
                          ),
                    }
                },
                data: jsonData.RECORDS.data
            }]
        };
    return(
      <Col span={24} style={{height:'100%'}}>
          <LineArrayCharts
            id='instructionalId'
            data={pieOption}
            height='100%'/>
      </Col>
    )
  }
}
