import React, {Component} from 'react';
import {Col} from 'antd';
import '../styles.less'
import BarEcharts from '../../../common/components/BaseEcharts/BarEcharts';

import PieEcharts from '../../../common/components/BaseEcharts/PieEcharts';
import LineArrayCharts from '../../../common/components/BaseEcharts/LineArrayCharts';
import echarts from 'echarts';
import jsonData from '../../../data/course/课程视角--课程教学情况-开课学生.json';
import jsonData1 from '../../../data/course/课程视角--课程教学情况-2.json';
import jsonData2 from '../../../data/course/课程视角--课程教学情况-4.json'
export default class CourseTeaching extends Component {
  constructor(props) {
    super(props);
    this.state = {
      number:'12345',
      operationRate:'80%',
    }
  }
  render(){
    // 柱状图
    let option = {
      title: {
        show:false
      },
      xAxis: {
        data: jsonData.RECORDS.axis,
        axisLabel: {
          interval:0,
          textStyle: {
            color: '#00CFFF'
          }
        }
      },
      yAxis: {
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          show:false
        },
        splitLine:{
            // show:false,
            lineStyle: {
              color: "#379fb7",
              type: "dashed"
            }
          }
      },
      series: [
        {
          type: 'bar',
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(
                0, 0, 0, 1,
                [
                  {offset: 0, color: 'rgba(0,207,255,1)'},
                  {offset: 0.5, color: 'rgba(0,207,255,0.8)'},
                  {offset: 1, color: 'rgba(0,207,255,0.19)'}
                ]
              )
            }
          },
          label: {
            normal: {
                show: true,
                position: 'top',
                color: '#FFF',
            }
          },
          data: jsonData.RECORDS.data
        }
      ]
    };


    // 2
        let pieOption = {
            title:{
              textStyle:{
                  color:"#6a9cd5",
              },
              left:"center",
            },
            tooltip: {
                trigger:'axis',
                formatter:"{b} : {c} "
            },
            grid: {//统计图距离边缘的距离
                top: '8%',
                left: '10%',
                right: '10%',
                bottom: '28%'
            },
            xAxis: [{
                type: 'category',
                boundaryGap: false,
                axisLine: {
                    show: true,
                    lineStyle: {
                        color: '#233e64'
                    },
                },
                axisLabel : {
                    textStyle: {
                        color: '#00CFFF',
                    },
                    formatter : function(params){
                        var newParamsName = "";
                        var paramsNameNumber = params.length;
                        var provideNumber = 5;
                        var rowNumber = Math.ceil(paramsNameNumber / provideNumber);
                    if (paramsNameNumber > provideNumber) {
                        for (var p = 0; p < rowNumber; p++) {
                            var tempStr = "";
                            var start = p * provideNumber;
                            var end = start + provideNumber;
                            if (p === rowNumber - 1) {
                                tempStr = params.substring(start, paramsNameNumber);
                            } else {
                                tempStr = params.substring(start, end) + "\n"; } newParamsName += tempStr;
                            }
                    } else {
                            newParamsName = params;
                        }
                        return newParamsName
                    }
                },
                // axisTick: { show: true,},//刻度点数轴
                data: jsonData1.RECORDS.axis,
            }],
            yAxis:{
                axisLine: {
                show: false
                },
                axisTick: {
                    show: false
                },
                axisLabel: {
                  color:'#00CFFF'
                },
                splitLine:{
                    // show:false,
                    lineStyle: {
                      color: "#379fb7",
                      type: "dashed"
                    }
                }
            },
            series: [{
                name: '',
                type: 'line',
                smooth: true,
                symbolSize:0,
                lineStyle: {
                    normal: {
                        color: "#3deaff"
                    }
                },
                areaStyle: {
                    normal: {
                        color: new echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                              {offset: 0, color: 'rgba(0,207,255,1)'},
                              {offset: 0.5, color: 'rgba(0,207,255,0.8)'},
                              {offset: 1, color: 'rgba(0,207,255,0.19)'}
                            ]
                          ),
                    }
                },
                data: jsonData1.RECORDS.data
            }]
        };
    // 3
    let legendData = jsonData2.RECORDS;
    let total = legendData.map(item=>item.value).reduce((a,b)=>a+b);
    let pieOption1 = {
      title:{
        text: '作业',
        left: '23%',
        top: '36%',
        textStyle: {
          color: '#00CFFF',
          fontSize: '22',
          align: 'center',
        },
      },
      tooltip: {
        trigger: 'item',
        formatter: "{b}: {c} ({d}%)"
      },
      legend: {
        orient: 'vertical',
        x: '60%',
        y: 'center',
        icon: 'circle',
        itemWidth: 5,
        itemHeight: 5,
        itemGap: 13,
        textStyle: {
          color: '#00CFFF',
        },
        data:legendData,
        formatter: (name) => {
            let cur = legendData.filter(item=>item.name===name)[0];
            let persent = Math.round(cur.value/total * 100 )+ "%";
            return name + ":" + cur.value + `(${persent})`;
        }
      },
      color:['#00CFFF','#13C2C2','#2FC25B','#FACC14','#F04864'],
      series: [
        {
          name:'年龄',
          type:'pie',
          radius: ['50%', '70%'],
          center: ['30%', '45%'],
          avoidLabelOverlap: false,
          label: {
            color:'#00CFFF',
            normal: {
              show: false
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: '30',
                fontWeight: 'bold'
              }
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data:jsonData2.RECORDS
        }
      ]
    };
    return(
      <Col span={24} style={{height:'100%'}}>
        <Col span={8} style={{height:'100%'}}>
            <BarEcharts
              id='schoolNeginsStuId'
              data={option}
              height='100%'/>
        </Col>
        <Col span={8} style={{height:'100%'}}>
          <LineArrayCharts
            id='schoolNeginsStuPieId'
            data={pieOption}
            height='100%'/>
        </Col>
        <Col span={8} style={{height:'100%'}}>
        
          <PieEcharts
            id='schoolNeginsStuPie2Id'
            data={pieOption1}
            height='100%'/>
        </Col>
      </Col>
    )
  }
}
