import React, {Component} from 'react';
import {Col} from 'antd';
import "../styles.less"
import BarEcharts from '../../../common/components/BaseEcharts/BarEcharts';
import echarts from 'echarts';
import jsonData from '../../../data/course/课程视角--课程指数及排名.json';
export default class CurriculumIndexRanking extends Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }
  render(){
    let option = {
      title: {
        show:false
      },
      xAxis: {
        data: jsonData.RECORDS.axis,
        axisLabel: {
          interval:0,
          textStyle: {
            color: '#00CFFF'
          }
        }
      },
      yAxis: {
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          show:false
        },
        splitLine:{
          // show:false,
          lineStyle: {
            color: "#379fb7",
            type: "dashed"
          }
        }
      },
      series: [
        {
          type: 'bar',
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(
                0, 0, 0, 1,
                [
                  {offset: 0, color: 'rgba(0,207,255,1)'},
                  {offset: 0.5, color: 'rgba(0,207,255,0.8)'},
                  {offset: 1, color: 'rgba(0,207,255,0.19)'}
                ]
              )
            }
          },
          label: {
            normal: {
                show: true,
                position: 'top',
                color: '#FFF',
            }
          },
          data: jsonData.RECORDS.data
        }
      ]
    }
    return(
      <Col span={24} style={{height:'100%'}}>
        <BarEcharts
            id='indexRankingId'
            data={option}
            height='100%'/>
      </Col>
    )
  }
}
