import React, { Component } from 'react';
import { Row, Col } from 'antd';
import '../styles.less'
export default class SchoolBeginsCondition extends Component {
  constructor(props){
    super(props);
    this.state = {
        standard:'有',
        material:'汽车构造与商务评价',
        plan:'汽车构造与商务计划',
        teachingPlan:'齐全',
        totalResources:'52333',
        problemNum:'6662',
        graphicImage:'7222',
        audioQuantity:'1333',
        videoNum:'230',
        activity:'220',
        trialBalloon:'300', 
    }
  }
  render() {
    return (
      <Row type="flex" align="middle" style={{paddingLeft:'5%'}}>
        <Col span={24} className={'major_visual_basic_info_content'}>
            <div className={'school_begins'}>
                <label className={'school_begins_text_align'}>课程标准：</label>
                    <span className={'school_begins_count'}>{this.state.standard}</span>
                <label className={'school_begins_text_align'}>课程教材：</label>
                    <span className={'school_begins_count'}>{this.state.material}</span>
                <label className={'school_begins_text_align'}>教学计划：</label>
                    <span className={'school_begins_count'}>{this.state.plan}</span>
            </div>
            <div  className={'school_begins'}>
                <label className={'school_begins_text_align'}>授课教案：</label>
                    <span className={'school_begins_count'}>{this.state.teachingPlan}</span>
                <label className={'school_begins_text_align'}>学习资源总数：</label>
                    <span className={'school_begins_count'}>{this.state.totalResources}</span>
            </div>
            <div  className={'school_begins'}>
                <label className={'school_begins_text_align'}>习题数：</label>
                    <span className={'school_begins_count'}>{this.state.problemNum}</span>
                <label className={'school_begins_text_align'}>图形图像：</label>
                    <span className={'school_begins_count'}>{this.state.graphicImage}</span>
                <label className={'school_begins_text_align'}>音频数量：</label>
                    <span className={'school_begins_count'}>{this.state.audioQuantity}</span>
            </div>
            <div  className={'school_begins'}>
                <label className={'school_begins_text_align'}>视频数：</label>
                    <span className={'school_begins_count'}>{this.state.videoNum}</span>
                <label className={'school_begins_text_align'}>教学活动数：</label>
                    <span className={'school_begins_count'}>{this.state.activity}</span>
                <label className={'school_begins_text_align'}>试题库：</label>
                    <span className={'school_begins_count'}>{this.state.trialBalloon}</span>
            </div>
        </Col>
      </Row>
    )
  }
}
