// 实践教学
import React, { Component } from 'react';
import { Col } from 'antd';
import BarEcharts from '../../../common/components/BaseEcharts/BarEcharts';
import jsonData from '../../../data/course/课程视角--实训教学.json';

export default class PracticeTeaching extends Component {
  

  render() {
    let option = {
      title: {
        show: false,
      },
      xAxis: {
        data: jsonData.RECORDS.axis,
        axisLabel: {
          interval: 0,
          textStyle: {
            color: '#00CFFF',
          },
        },
      },
      legend: {
        icon: 'circle',
        itemWidth: 14,
        itemHeight: 5,
        itemGap: 13,
        bottom: '4%',
        textStyle: {
          fontSize: 12,
          color: '#00cfff'
        }
      },
      grid: {
        top: '10%',
        left: '3%',
        right: '3%',
        bottom: '15%',
        containLabel: true
      },
      color: ['#00CFFF','#BD10E0'],
      yAxis: {
        axisLine: {
          show: false,
        },
        axisTick: {
          show: false,
        },
        axisLabel: {
          show: false,
        },
        splitLine: {
          lineStyle: {
            color: '#379fb7',
            type: 'dashed',
          },
        },
      },
      series: [
        {
          name:'实训项目数',
          type: 'bar',
          barGap: 0,
          data: jsonData.RECORDS.practice,
          label: {
            normal: {
              color: '#fff',
              show: true,
              position: 'top',
            },
          },
        },
        {
          name:'开出项目数',
          type: 'bar',
          barGap: 0,
          data: jsonData.RECORDS.open,
          label: {
            normal: {
              color: '#fff',
              show: true,
              position: 'top',
            },
          },
        },
      ],
    };
    return (
      <Col span={24} style={{height:'100%'}}>
        <BarEcharts
          id='PracticeTeachingBar'
          data={option}
          height='100%'/>
      </Col>
    );
  }
}
