/**
 *柱状图
 */
import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import {Col} from 'antd';
import echarts from 'echarts';
import TipInfoComp from './TipInfoComp'


class LineArrayCharts extends Component {
    

    

    render() {
        let chartData = this.props.data;
        if (chartData && chartData !== 'nodata') {
            // const axisColor = '#FFF';
            let option = {
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'shadow'
                    }
                },
                grid: {
                    left: '5%',
                    right: '8%',
                    bottom: '8%',
                    top: 50,
                    containLabel: true
                },
                legend: {
                    show: false,
                    data: [],
                },
                dataZoom: [],
                xAxis: [],
                yAxis: [],
                series: []
            };
            if (chartData.legend) {
                option.legend = chartData.legend;
            }
            // 处理图例
            if (chartData.tooltip) {
                option.tooltip = chartData.tooltip;
            }
            // 处理数据区域缩放
            if (chartData.dataZoom) {
                option.dataZoom = chartData.dataZoom;
            }
            // 处理title
            if (chartData.title) {
                option.title = chartData.title;
            }
            // grid
            if (chartData.grid) {
                option.grid = chartData.grid;
            }
            // 处理color
            if (chartData.color) {
                option.color = chartData.color;
            }
            // 处理X轴
            if (chartData.xAxis) {
                option.xAxis = chartData.xAxis;
            }
            // 处理Y轴
            if (chartData.yAxis) {
                option.yAxis = chartData.yAxis;
            }
            option.series = chartData.series;
            let id = this.props.id;
            let Interval = setInterval(() => {
                if (document.getElementById(id)) {
                    clearInterval(Interval);
                    let myCharts = echarts.init(document.getElementById(id));
                    myCharts.off('click');
                    myCharts.resize();
                    myCharts.setOption(option);
                    /**
                     * 点击统计图
                     */
                    if (this.props.clickChart) {
                        myCharts.on('click', (params) => {
                            this.props.clickChart(params);
                        })
                    }
                }
            }, 30)
        }
        return (
            <div style={{height: this.props.height ? this.props.height : '300px'}}>
                {chartData && chartData !== 'nodata' ? <Col span={24} className="bar_echarts" id={this.props.id}
                                                            style={{height: this.props.height ? this.props.height : '300px'}}>
                </Col> : <TipInfoComp data={chartData} />}
            </div>
        )
    }
}

export default withRouter(LineArrayCharts)