// 考试成绩
import React, { Component } from 'react';
import { Col } from 'antd';
import LineEcharts from '../../../common/components/BaseEcharts/LineEcharts';
import BarEcharts from '../../../common/components/BaseEcharts/BarEcharts';
import jsonData from '../../../data/course/课程视角--考试成绩--考试成绩1.json';
import jsonData1 from '../../../data/course/课程视角--考试成绩--考试成绩2.json';

export default class ExaminationResults extends Component {
  
  render() {
    let lineOption = {
      title: {
        show: false
      },
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        icon: 'circle',
        itemWidth: 14,
        itemHeight: 5,
        itemGap: 13,
        bottom: '4%',
        textStyle: {
          fontSize: 12,
          color: '#00cfff'
        }
      },
      grid: {
        top: '10%',
        left: '3%',
        right: '3%',
        bottom: '15%',
        containLabel: true
      },
      toolbox: {
        feature: {
          saveAsImage: {}
        }
      },
      xAxis: {
        axisLabel: {
          interval:0,
          textStyle: {
            color: '#00CFFF'
          }
        },
        data: jsonData.RECORDS.axis
      },
      yAxis: {
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          color:'#00CFFF'
        },
        splitLine:{
          // show:false,
          lineStyle: {
            color: "#379fb7",
            type: "dashed"
          }
        }
      },
      color:['#30CCF4','#DED324'],
      series: [
        {
          name:'及格率',
          type:'line',
          data:jsonData.RECORDS.pass
        },
        {
          name:'优秀率',
          type:'line',
          data:jsonData.RECORDS.excellent
        }
      ]
    };
    let option = {
      title: {
        show: false,
      },
      xAxis: {
        data: jsonData1.RECORDS.axis,
        axisLabel: {
          interval: 0,
          textStyle: {
            color: '#00CFFF',
          },
        },
      },
      legend: {
        icon: 'circle',
        itemWidth: 14,
        itemHeight: 5,
        itemGap: 13,
        bottom: '4%',
        textStyle: {
          fontSize: 12,
          color: '#00cfff'
        }
      },
      grid: {
        top: '10%',
        left: '3%',
        right: '3%',
        bottom: '15%',
        containLabel: true
      },
      color: ['#00CFFF','#BD10E0'],
      yAxis: {
        axisLine: {
          show: false,
        },
        axisTick: {
          show: false,
        },
        axisLabel: {
          show: false,
        },
        splitLine: {
          lineStyle: {
            color: '#379fb7',
            type: 'dashed',
          },
        },
      },
      series: [
        {
          name:'期末考试平均成绩',
          type: 'bar',
          data: jsonData1.RECORDS.end,
          barGap: 0,
          label: {
            normal: {
              color: '#fff',
              show: true,
              position: 'top',
            },
          },
        },
        {
          name:'总评平均成绩',
          type: 'bar',
          data: jsonData1.RECORDS.all,
          label: {
            normal: {
              color: '#fff',
              show: true,
              position: 'top',
            },
          },
        },
      ],
    };
    return (
      <Col span={24} style={{height:'100%'}}>
        <Col span={12} style={{height:'100%'}}>
          <LineEcharts
            id='ExaminationResultsLine'
            data={lineOption}
            height='100%'/>
        </Col>
        <Col span={12} style={{height:'100%'}}>
          <BarEcharts
            id='ExaminationResultsBar'
            data={option}
            height='100%'/>
        </Col>
      </Col>
    );
  }
}
